/* General Styles */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #000;
  color: white;
  margin: 0;
  padding: 0;
}

/* Header & Navigation */
header {
  display: flex;
  justify-content: space-between;
  padding: 20px 50px;
}

nav a {
  margin: 0 15px;
  text-decoration: none;
  color: white;
  font-weight: 500;
}

nav a:hover,
nav a.active {
  color: #e63946;
  border-bottom: 2px solid #e63946;
  padding-bottom: 3px;
}

/* Home Section */
.home {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: left;
  padding: 20px;
  position: relative;
}

.profile-container {
  position: relative;
}

.profile-container h2 {
  position: absolute;
  top: -40px;
  left: 0;
  color: #e63946;
  font-size: 18px;
}

.profile-pic {
  width: 250px;
  height: auto;
  border-radius: 10px;
}

.content {
  margin-left: 50px;
  max-width: 600px;
}

.content h1 {
  font-size: 48px;
}

.dynamic-text {
  font-size: 36px;
  font-weight: bold;
  color: #e63946;
}

.typing-cursor {
  color: #e63946;
  animation: blink 1s infinite;
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* Social Links */
.social-links a {
  margin-right: 15px;
  color: white;
  text-decoration: none;
  font-size: 16px;
}

.social-links a:hover {
  color: #e63946;
}

/* Hire Button */
.hire-button {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 24px;
  border: 1px solid #e63946;
  color: #e63946;
  text-decoration: none;
  border-radius: 5px;
  font-weight: 500;
}

.hire-button:hover {
  background-color: #e63946;
  color: white;
}

/* Section General */
.section {
  padding: 50px;
}

.section h2 {
  color: #e63946;
  font-size: 28px;
  margin-bottom: 15px;
}

/* Education Table */
table {
  width: 100%;
  border-collapse: collapse;
  background-color: #111;
  margin-top: 20px;
}

th, td {
  border: 1px solid #444;
  padding: 12px;
  text-align: center;
  color: white;
}

th {
  background-color: #e63946;
  color: white;
}

/* Hobby Video */
video {
  display: block;
  margin: 0 auto;
  width: 90%;
  max-height: 500px;
  border-radius: 10px;
  margin-top: 15px;
}

/* Red Circle Background - Optional */
.profile-container::before {
  display: none;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  header {
    flex-direction: column;
    align-items: center;
  }
  nav a {
    display: block;
    margin: 5px 0;
  }
  .home {
    flex-direction: column;
    text-align: center;
  }
  .profile-container h2 {
    position: static;
    margin-bottom: 10px;
  }
  .profile-pic {
    width: 150px;
  }
  .content {
    margin-left: 0;
  }
  .dynamic-text,
  .typing-cursor {
    font-size: 18px !important;
  }
  video {
    width: 100%;
    height: auto;
  }
  table {
    font-size: 12px;
  }
  .hire-button {
    padding: 8px 16px;
    font-size: 14px;
  }
  .section {
    padding: 20px;
  }
}

/* Light mode overrides */
.light-mode {
  background-color: #fff;
  color: #000;
}

.light-mode header {
  background-color: transparent;
  color: #000;
}

.light-mode nav a {
  color: #000;
}

.light-mode nav a:hover,
.light-mode nav a.active {
  color: #e63946;
  border-bottom: 2px solid #e63946;
}

.light-mode .content h1,
.light-mode .dynamic-text,
.light-mode .typing-cursor {
  color: #000;
}

.light-mode .hire-button {
  border: 1px solid #e63946;
  color: #e63946;
  background-color: #fff;
}

.light-mode table {
  background-color: #fff;
}

.light-mode th,
.light-mode td {
  color: #000;
}

.light-mode th {
  background-color: #e63946;
  color: #fff;
}

/* Theme toggle button */
#theme-toggle {
  position: fixed;
  top: 80px;
  right: 20px;
  padding: 10px 15px;
  background-color: #e63946;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1000;
}

#theme-toggle:hover {
  background-color: #c5303e;
}

/* Back to top button */
#back-to-top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #e63946;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}

#back-to-top:hover {
  background-color: #c5303e;
}

.tech-stack {
  margin-top: 20px;
  padding: 10px;
  border-top: 1px solid #333;
  display: flex;
  align-items: center;
  gap: 10px;
}

.tech-stack p {
  margin: 0;
  font-weight: bold;
}

.tech-stack img {
  height: 28px;
} 